const CenteredSVG = ({ label, href, cursorColor }) => {
    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 1200 300" // Adjusted to fit larger content
            width="100%"
            height="auto"
            className="responsive-svg"
        >
            {/* Group to center all elements */}
            <g transform="translate(600, 150)" textAnchor="middle">
                {/* "walkum" Text */}
                <text
                    x="-300" // Offset to the left
                    fill="white"
                    fontFamily="Courier"
                    fontSize="90px"
                >
                    walkum
                </text>
                {/* Blinking Cursor */}
                <text
                    x="0"
                    fill={cursorColor}
                    fontFamily="Courier"
                    fontSize="90px"
                    fontWeight="bold"
                    className="blinking-cursor"
                    transform="scale(1, 5)" // Scale 1.5 times taller
                >
                    |
                </text>
                {/* Clickable Label */}
                <a href={href}>
                    <text
                        x="300" // Offset to the right
                        className="svg-button"
                        fontFamily="Courier"
                        fontSize="90px"
                    >
                        {label}
                    </text>
                </a>
            </g>
        </svg>
    );
};

export default IndexPage;

